          <!--<text class="all">All</text>-->
        </view>
        <!-- 元素列表 -->
        <view class="grid-container">
          <!-- -------- 第一个元素：Service Offering -------- -->
          <view class="grid-item" @click.stop="handleClickItem(1, 1)">
            <image
              :src="img_offering_serviceOffering"
              class="grid-icon"
              mode="widthFix"
            />
            <text>Service</text>
            <text>Offering</text>
          </view>
          <!-- -------- 第二个元素：Goods Offering -------- -->
          <view class="grid-item" @click.stop="handleClickItem(2, 1)">
            <image
              :src="img_offering_goodsOffering"
              class="grid-icon"
              mode="widthFix"
            />
            <text>Goods</text>
            <text>Offering</text>
          </view>
          <!-- -------- 第三个元素：My Listings -------- -->
          <view class="grid-item" @click.stop="handleClickMyListings">
            <image
              :src="img_offering_myListings"
