              mode="widthFix"
            />
            <text>My</text>
            <text>Listings</text>
          </view>
        </view>
      </view>
      <z-paging
        ref="paging"
        v-model="topPickDataList"
        use-page-scroll
        :refresher-enabled="false"
        @query="onLoadTopPickList"
      >
