        <view class="section">
          <!-- 标题行文字 -->
          <view class="section-header">
            <text>Top Picks</text>
            <view class="tp_header_icon">
              <image
                :src="img_topPicks_titleSettingIcon"
                style="width: 100%"
                mode="widthFix"
                @click="showFilter = true"
              />
            </view>
          </view>
          <OrderCard
            v-for="(item, index) in topPickDataList"
            :dataSource="item"
            :key="index"
            @click="handleClickOrder(item)"
          />
        </view>
      </z-paging>
