  <CustomTabbar />
  <FilterPopup
    v-model="showFilter"
    @apply="handleApplyFilter"
    @reset="handleResetFilter"
  />
</template>

<script setup>
import { ref } from "vue"
// 图片导入
import img_messageIcon from "@/static/home/messageIcon.png"

import img_looking_serviceReques from "@/static/home/1.png"
import img_looking_goodsRequest from "@/static/home/2.png"
import img_looking_reqestHistory from "@/static/home/3.png"

import img_offering_serviceOffering from "@/static/home/4.png"
import img_offering_goodsOffering from "@/static/home/5.png"
import img_offering_myListings from "@/static/home/6.png"

import img_topPicks_titleSettingIcon from "@/static/home/topPicks_titleSettingIcon.png"

import FilterPopup from "@/components/FilterPopup.vue"
import OrderCard from "@/components/OrderCard.vue"
import CustomTabbar from "@/components/CustomTabbar.vue"

const userStore = useUserStore()
const userInfo = computed(() => userStore.userInfo)
const showFilter = ref(false)

const paging = ref()
const topPickDataList = ref([])

onShow(() => paging.value?.reload())

onLoad(async (options) => {
  const NestedObjectOptions = flattenToNestedObject(options)
  console.log("NestedObjectOptions", NestedObjectOptions)
})

const searchText = ref("")

const handleClickItem = (method, type) => {
  useNavigate("/pages/request/category", {
    type: type,
    method: method,
  })
}

const queryFrom = ref({})
// ==================================== 展示待做信息 ====================================
const handleMessageIcon = () => {
  useNavigate("/pages/communication/index")
}

const handleSearch = () => {
  useNavigate("/pages/search/result", { keyword: searchText.value })
  searchText.value = ""
}

const handleClickMyListings = () => {
  useNavigate("/pages/myorder/index", { type: 1 })
}
const handleClickMyHistory = () => {
  useNavigate("/pages/myorder/index", { type: 2 })
}

const handleApplyFilter = (event) => {
  queryFrom.value = {
    methodin: event.methodin,
    pricein: event.pricein,
    to: event.to,
  }

  nextTick(() => {
    paging.value.reload()
  })
}

const handleResetFilter = () => {
  queryFrom.value = {}
  paging.value.reload()
}

const handleClickOrder = (item) => {
  useNavigate("/pages/tracking/info", {
    id: item.id,
    match: 1,
  })
}
/**
 * 分页搜索
 */
const onLoadTopPickList = async (page, rows) => {
  if (queryFrom.value.pricein && Array.isArray(queryFrom.value.pricein)) {
    if (
      queryFrom.value.pricein.length == 2 &&
      queryFrom.value.pricein[1] > queryFrom.value.pricein[0]
    ) {
      queryFrom.value.pricein = queryFrom.value.pricein.join(",")
    } else {
      queryFrom.value.pricein = ""
    }
  }

  const res = await indexOrders({
    page,
    rows,
    ...queryFrom.value,
  })
  paging.value.complete(res.data.data)
}

</script>

